.App {
  font-family: Arial, sans-serif;
  margin: 20px;
  padding: 20px;
}

h1 {
  color: #333;
}

header, section {
  margin-bottom: 40px;
}

h2 {
  margin-bottom: 10px;
  font-size: 1.5em;
}

h3 {
  color: #555;
}

a {
  color: #1a0dab;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

ul {
  list-style-type: none;
  padding: 0;
}

ul li {
  margin: 5px 0;
}

button {
  padding: 10px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

button:hover {
  background-color: #0056b3;
}

/* Gradient Background */
body {
  margin: 0;
  padding: 0;
  height: 100vh;
  background: linear-gradient(45deg, #ffffff, #b0b0b0); /* Initial gradient from white to grey */
  background-size: 400% 400%;  /* Make the gradient larger than the screen */
  animation: gradientBackground 10s ease infinite;  /* Animation timing and loop */
}

@keyframes gradientBackground {
  0% {
    background-position: 0% 50%;  /* Starting point of the gradient */
  }
  50% {
    background-position: 100% 50%;  /* Midpoint of the gradient animation */
  }
  100% {
    background-position: 0% 50%;  /* Ending point of the gradient */
  }
}
